* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
}

input {
  color: rgb(6, 56, 78);
  font-size: 1.2rem;
  width: 100%;
}
.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card {
  align-content: center;
  border: 1px solid rgba(2, 58, 104, 0.418);
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}
.card p {
  text-align: center;
  padding: 5px;
}

.card img {
  max-height: 170px;
}
.titulo {
  text-transform: capitalize;
}
